import React, { Component } from 'react'
import './Hangman.css'
import img0 from './0.jpg'
import img1 from './1.jpg'
import img2 from './2.jpg'
import img3 from './3.jpg'
import img4 from './4.jpg'
import img5 from './5.jpg'
import img6 from './6.jpg'
import { randomWord } from './words'
import AlphaButtons from './AlphaButtons'

class Hangman extends Component {
  static defaultProps = {
    maxWrong: 6,
    images: [img0, img1, img2, img3, img4, img5, img6]
  }

  constructor(props) {
    super(props)
    this.state = {
      nWrong: 0,
      guessed: new Set(),
      answer: randomWord()
    }

    this.handleGuess = this.handleGuess.bind(this)
    this.handleGame = this.handleGame.bind(this)
  }

  guessedWord() {
    return this.state.answer
      .split('')
      .map((letter) => (this.state.guessed.has(letter) ? letter : '_'))
  }

  handleGuess(e) {
    const letter = e.target.value

    this.setState((st) => ({
      guessed: st.guessed.add(letter),
      nWrong: st.nWrong + (st.answer.includes(letter) ? 0 : 1)
    }))
  }

  handleGame(e) {
    const status = e.target.textContent.includes('Reset')

    this.setState((prevState) => {
      return {
        nWrong: 0,
        guessed: new Set(),
        answer: status ? prevState.answer : randomWord()
      }
    })
  }

  render() {
    const remainingGuesses = this.props.maxWrong - this.state.nWrong
    const guessesLeft = remainingGuesses !== 0

    const isGuessed = this.guessedWord().join('') === this.state.answer

    return (
      <div className='Hangman'>
        <h1>Hangman</h1>

        {!isGuessed ? (
          <div>
            {guessesLeft && (
              <img
                src={this.props.images[this.state.nWrong]}
                alt={`${this.state.nWrong} wrong guesses`}
              />
            )}

            <p className='Hangman-remaining'>
              {guessesLeft
                ? `${remainingGuesses} guesses left`
                : `YOU LOSE! The correct word is "${this.state.answer}"`}
            </p>

            {guessesLeft && (
              <div>
                <p className='Hangman-word'>{this.guessedWord()}</p>

                <AlphaButtons
                  handleGuess={this.handleGuess}
                  guessed={this.state.guessed}
                />
              </div>
            )}
          </div>
        ) : (
          <h2 className='hurray'>
            HURRAY! You've guessed the word "{this.state.answer.toUpperCase()}"
          </h2>
        )}

        <button onClick={this.handleGame} className='reset-btn'>
          Reset Game
        </button>

        <button onClick={this.handleGame} className='reset-btn'>
          New Game
        </button>
      </div>
    )
  }
}

export default Hangman



